// Component :: Callouts

.c-callout {
  background: _color(grey, 200);
  border: 1px solid _color(grey, 500);;
  border-radius: 4px;
  padding: 10px 12px;

  *:last-child {
    margin-bottom: 0;
  }

  &--yellow {
    background: _color(yellow, 200);
    border-color: _color(yellow, 400);

    .c-badge {
      background-color: _color(yellow, 600);
    }

    p {
      color: _color(yellow, 800);
      line-height: 1.3;
    }
  }
}
