// Components :: Asset container

figure {
  margin-bottom: 2em;

  &.c-example-preview {
    background: _color(white);
    border: 1px solid _color(grey, 500);
    border-radius: 3px 3px 0 0;
    bottom: -2px;
    margin-bottom: 0;
    margin-top: 2em;
    position: relative;

    #post-content {
      max-width: 850px;

      .browser-bar {
        margin: 0;
      }
    }
  }
}

img { max-width: 100%; }

.c-asset-container {
  border-radius: 3px;
  position: relative;
  text-align: center;

  &__bordered {
    border: 1px solid _color(grey, 500);
    img {
      margin: 18% auto;
      max-width: 75%;
    }
  }

  img {
    border-radius: 3px;
  }
}
