// Layouts :: Styleguide (Nav)

.hs-nav-side {
  background-color: white;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  width: 100%;
  z-index: 3;

  @include breakpoint(md) {
    bottom: 0;
    position: fixed;
    top: 0;
    width: 220px;
  }
  @include breakpoint(lg) {
    width: 280px;
  }

  &__header {
    position: relative;
  }

  &__toggle {
    filter: blur(0);
    padding: 10px;
    position: absolute;
    left: 5px;
    transform: translateY(-50%);
    top: 50%;
    z-index: 3;

    @include breakpoint(md) {
      display: none;
    }
  }

  &__body {
    display: none;
    flex: 1;
    position: relative;

    @include breakpoint(md) {
      display: block;
    }
  }

  &__content {
    @include breakpoint(md) {
      bottom: 0;
      overflow: auto;
      position: absolute;
      top: 0;
      width: 100%;
    }
  }

  .c-list__active {
    color: _color(charcoal, 300);
    &:hover {
      color: _color(charcoal, 500);
    }
  }

  .nav-secondary__item > a {
    color: _color(charcoal, 200);
    &:hover {
      color: _color(charcoal, 500);
    }
  }

  .c-nav-secondary__active {
    background: darken(_color(grey, 300), 3%); 
    box-shadow: 5px 0 0 _color(blue,500) inset;
    &:hover {
      color: _color(blue, 500) !important;
    }
  }

  .c-nav-third__active {
    color: _color(charcoal, 200);
    &:hover {
      color: _color(charcoal, 500);
    }
  }
}
