// Layouts :: Content

.s-hs-docs,
.s-content {

  & > h1,
  & > h2 {
    font-weight: 400;
  }

  & > h3 {
    font-weight: 600;
  }

  & > h2,
  & > h3 {
    margin-top: 45px;
  }

  & > h4,
  & > h5 {
    margin-top: 20px;
  }

  & > h1,
  & > h2,
  & > h3,
  & > h4,
  & > h5 {
    color: darken(_color(charcoal, 800), 5%);
    line-height: 1.2;
    margin-bottom: 20px;
  }

  & > h2 + h3,
  & > h3 + h4 {
    margin-top: 30px;
  }

  & > h2 {
    font-size: 1.5em;
  }

  & > h3 {
    font-weight: 500;
    font-size: 1.15em;
  }

  & > h4 {
    color: _color(charcoal, 600);
    font-size: 0.8em;
    font-weight: bold;
    letter-spacing: 0.5px;
    text-transform: uppercase;

    + p {
      font-size: 85%;
      margin-top: -10px;
    }
  }

  .hs-content__top + p {
    color: _color(charcoal, 600);
    font-size: 120%;
    font-weight: 400;
    letter-spacing: -0.01rem;
  }

  & > p {
    color: _color(charcoal, 300);
    line-height: 1.5;
    margin-bottom: 1.5em;
    // max-width: 780px;

    + .highlighter-rouge {
      margin-top: -0.5em;
    }
  }

  & > ul,
  & > ol {
    color: _color(charcoal, 300);
  }

  hr {
    border-top-color: _color(grey, 500);
    margin: 60px 0;
  }

  table {
    color: _color(charcoal, 500);
    font-size: 0.95rem;
  }

  // Kramdown table of contents
  @at-root {
    // Unfortunately, there isn't any way to use class names instead of
    // #id for Kramdown's auto-generated table of contents component
    // Hide table of contents header
    #contents {
      color: _color(charcoal, 300);
      font-size: 0.8rem;
      font-weight: 500;
      letter-spacing: 0.3px;
      margin-top: 15px;
      text-transform: uppercase;
    }

    #markdown-toc {
      margin-bottom: 32px;

      > li:first-child {
        display: none;
      }
    }
  }
}
