// Sections :: Scouticons

.s-hs-docs--scouticons {
  .c-icon-list {
    position: relative;

    &__item {
      border: 1px solid _color(grey, 400);
      border-radius: 4px;
      display: inline-block;
      margin: 0 10px 10px 0;
      padding-top: 30px;
      text-align: center;
      vertical-align: top;
      width: 24%;

      @include breakpoint(sm) {
        margin-bottom: 15px;
        width: 165px;
      }

      .icon {
        font-size: 24px;
      }

      span {
        background: _color(grey, 200);
        border-top: 1px solid _color(grey, 400);
        color: _color(charcoal, 300);
        display: block;
        font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
        font-size: 0.7rem;
        margin-top: 30px;
        padding: 4px 16px;
      }
    }
  }

  h4 + p {
    min-height: 50px;
  }
}
